.fashion {
  &__wrapper {
    padding: 50px 0;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__container {
    display: flex;
    overflow: hidden;
    width: 100%;
    position: relative;
  }
  &__items {
    display: flex;
    justify-content: space-between;
    width: 100%;
  }
  &__item {
    padding-bottom: 10px;
    margin-bottom: 10px;
    display: flex;
    flex-direction: column;
    position: relative;
    width: 23%;
    transition: .3s;
  }
  &__item:hover {
    -webkit-box-shadow: 5px 5px 5px -5px rgba(34, 60, 80, 0.6);
    -moz-box-shadow: 5px 5px 5px -5px rgba(34, 60, 80, 0.6);
    box-shadow: 5px 5px 5px -5px rgba(34, 60, 80, 0.6);
    transition: .2s;
  }
  &__item:hover &__cart-wrap {
    display: flex;
  }
  &__illustrations {
    display: flex;
    overflow: hidden;
    position: relative;
  }
  &__illustrations-wrap {
    display: flex;
  }
  &__illustration {
    flex-shrink: 0;
  }
  &__brand {
    display: block;
    padding-top: 30px;
    font-size: 12px;
    line-height: 1.2;
    font-weight: 300;
    color: #22262a;
  }
  &__title{
    padding: 25px 0;
    font-size: 15px;
    line-height: 1.2;
    font-weight: 600;
    color: #22262a;
  }
  &__second-prace {
    font-size: 15px;
    line-height: 1.2;
    font-weight: 500;
    color: #373737;
    margin-right: 10px;
  }
  &__new-prace {
    font-size: 15px;
    line-height: 1.2;
    font-weight: 600;
    color: #cf2929;
  }
  &__cart-wrap {
    position: absolute;
    z-index: 1;
    bottom: 10px;
    right: 10px;
    display: flex;
    justify-content: flex-end;
    display: none;
  }
  &__button {
    width: 21px;
    height: 21px;
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    background-color: transparent;
    transition: .2s;
  }
  &__button:hover {
    
    transform: translateY(-3px) scale(1.3);
    transition: transform .2s;
  }
  &__button:first-child {
    background-image: url(../img/cart.svg);
    margin-right: 15px;
  }
  &__button:last-child {
    background-image: url(../img/heart.svg);
  }
}

.swiper-button-next,
  .swiper-button-prev {
    visibility: hidden;
  }
.swiper-button-next::after,
.swiper-button-prev::after {
  visibility: hidden;
}

@media screen and (max-width: 880px) {
  .fashion {
    &__cart-wrap {
      display: flex;
      justify-content: center;
      position: relative;
      bottom: initial;
    }
    &__button {
      height: 48px;
      width: 48px;
      margin: 15px 0;
    }
  }
}

@media screen and (max-width: 740px) {
  .fashion {
    &__wrapper {
      padding-bottom: 0;
    }
    &__container {
      padding-bottom: 100px;
    }
    &__item {
      justify-content: center;
    }
    &__item {
      width: 30%;
      flex-shrink: 0;
    }
    // &__cart-wrap {
    //   display: flex;
    //   justify-content: center;
    //   position: relative;
    //   bottom: initial;
    // }
    // &__button {
    //   height: 48px;
    //   width: 48px;
    //   margin: 15px 0;
    // }
    &__button:first-child {
      margin-right: 15px;
    }
  }
  .swiper-button-next,
  .swiper-button-prev {
    visibility: initial;
  }
  .swiper-button-next,
  .swiper-button-prev {
    visibility: initial;
    display: flex;
    position: absolute;
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    z-index: 20;
    width: 48px;
    height: 48px;
    top: auto;
  }
  .swiper-button-next {
    background-image: url("../img/arrow-right.svg");
    right: 50px;
    bottom: 45px;
  }
  .swiper-button-prev {
    background-image: url("../img/arrow-left.svg");
    left: 50px;
    bottom: 45px;
  }
}

@media screen and (max-width: 600px) {
  .fashion {
    &__item {
      width: 49%;
    }
  }
}
@media screen and (max-width: 450px) {
  .fashion {
    &__item {
      width: 100%;
    }
  }
}