.title {
  font-size: 20px;
  line-height: 1.2;
  font-weight: 700;
  color: #22262a;
  &_middle {
    font-size: 22px;
    line-height: 1.2;
    font-weight: 700;
    color: #22262a;
  }
  &_pre {
    font-size: 18px;
    margin: 30px 0;
    line-height: 1.2;
    font-weight: 400;
    color: #22262a;
  }
  &_big {
    font-size: calc(50px + (30 + 30 * .7) * ((100vw - 320px)/ 1600)); // 50 80px;
    line-height: 1.2;
    font-weight: 700;
    color: #22262a;
  }
}