.nav-menu {
  display: flex;
  &__list {
    display: flex;
  }
  &__link {
    padding: 10px 15px;
    display: inline-block;
    text-transform: uppercase;
    font-size: 18px;
    line-height: 1.2;
    font-weight: 400;
    color: #22262a;
    border-bottom: 2px solid transparent;
    transition: .2s;
  }
  &__link:hover {
    border-bottom: 2px solid #22262a;
    transition: .2s;
  }
  &__wrap-btn {
    display: flex;
    align-items: center;
  }
  &__btn {
    height: 21px;
    width: 21px;
    display: block;
    background-color: transparent;
    background-repeat: no-repeat;
    background-size: cover;
  }
  &__btn:not(:last-child) {
    margin-right: 18px;
  }
}

#like {
  background-image: url(../img/heart.svg);
}
#cart {
  background-image: url(../img/cart.svg);
}

@media screen and (max-width: 1000px) {
  .nav-menu {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    position: fixed;
    z-index: 3;
    padding-top: 110px;
    background-color: rgb(198, 204, 207);
    top: 100%;
    height: 100%;
    overflow: auto;
    transition: .3s;
    &_active {
      top: 0;
      transition: .3s;
    }
    &__list {
      flex-direction: column;
      width: 100%;
      justify-content: space-around;
      align-items: center;
    }
    &__item {
      display: flex;
      justify-content: center;
      align-items: center;
      height: 48px;
      min-width: 100px;
    }
    &__wrap-btn {
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
    }
    &__btn {
      margin-top: 15px;
      width: 48px;
      height: 48px;
    }
  }
}