.footer {
  background-color: #e7f5f8;
  &__wrapper {
    height: 95px;
  }
  &__title {
    text-align: center;
    font-size: calc(12px + (8 + 8 * .7) * ((100vw - 320px)/ 1600));  //12 20px;
    line-height: 95px;
    font-weight: 300;
    letter-spacing: 0.025em;
    color: #22262a;
  }
}