.search {
  display: flex;
  // flex-direction: row-reverse;
  position: relative;
  z-index: 0;
  &__input {
    padding: 5px 15px;
    left: 0;
    width: 0;
    position: absolute;
    transition: .3s;
  }
  &__input_active {
    border: 1px solid #22262a;
    border-radius: 4px;
    height: 21px;
    width: 145px;
    left: -145px;
    transition: .3s;
  }
  &__button {
    z-index: 2;
    margin: 0 15px;
    height: 21px;
    width: 21px;
    display: block;
    background-color: transparent;
    background-repeat: no-repeat;
    background-size: cover;
  }
}

#serc {
  background-image: url(../img/search.svg);
}

@media screen and (max-width: 1000px) {
  .search {
    width: 100%;
    padding: 20px 0;
    justify-content: flex-end;
    order: 1;
    &__input {
      height: 48px;
      width: calc(90vw - 60px);
      position: relative;
    }
    &__button {
      width: 48px;
      height: 48px;
    }
  }
}