.contacts {
  background-color: #f2f8fc;
  &__wrapper {
    padding: 100px 0;
    display: flex;
    justify-content: space-between;
  }
  &__text {
    padding-top: 30px;
    padding-bottom: 80px;
    font-size: 15px;
    line-height: 1.2;
    font-weight: 400;
    color: #22262a;
  }
  &__about-us {
    display: flex;
    flex-direction: column;
    max-width: 350px;
    width: 100%;
    margin-right: 30px;
  }
  &__we-are {
    display: flex;
    flex-direction: column;
  }
  &__addres-list {
    display: flex;
    width: max-content;
  }
  &__location {
    position: relative;
    margin-left: 31px;
    display: inline-block;
    font-size: 15px;
    line-height: 1.2;
    font-weight: 400;
    color: #22262a;
    border-bottom: 1px solid transparent;
  }
  &__location:hover {
    border-bottom: 1px solid #22262a;
  }
  &__location::before {
    content: url("../img/sprite.svg#location");
    width: 21px;
    height: 21px;
    display: block;
    position: absolute;
    left: -31px;
    top: -7px;
  }
  &__mail-tell {
    display: inline-block;
    width: max-content;
    padding: 10px 0;
    font-size: 15px;
    line-height: 1.2;
    font-weight: 600;
    color: #22262a;
    border-bottom: 1px solid transparent;
  }
  &__mail-tell:hover {
    border-bottom: 1px solid #22262a;
  }
  &__in-social {
    padding: 20px 0;
  }
  &__use-svg {
    height: 21px;
    width: 21px;
    fill: #22262ae0;
  }
  &__use-svg:not(last-child) {
    margin-right: 15px;
  }
  &__use-svg:hover {
    fill: black;
    -webkit-box-shadow: 0px 4px 5px 1px rgba(34, 60, 80, 0.6);
    -moz-box-shadow: 0px 4px 5px 1px rgba(34, 60, 80, 0.6);
    box-shadow: 0px 4px 5px 1px rgba(34, 60, 80, 0.6);
  }

  &__right-content {
    display: flex;
    flex-direction: column;
    width: 100%;
    max-width: 600px;
  }
  &__recive-email {
    width: 100%;
  }
  &__form {
    padding-top: 25px;
    padding-bottom: 40px;
    position: relative;
  }
  &__mail {
    height: 46px;
    width: 100%;
    background-color: #ffffff;
    border-radius: 5px;
    font-size: 15px;
    line-height: 1.2;
    font-weight: 400;
    color: #22262abe;
    padding: 15px 0;
    padding-left: 22px;
  }
  &__submit-btn {
    position: absolute;
    right: 22px;
    top: 40px;
    font-size: 17px;
    line-height: 1.2;
    font-weight: 600;
    color: #22262abe;
    background-color: transparent;
  }
  &__mail:focus {
    color: #22262a;
  }
  &__submit-btn:hover {
    color: black;
    -webkit-box-shadow: 0px 5px 5px -5px rgba(34, 60, 80, 0.6);
    -moz-box-shadow: 0px 5px 5px -5px rgba(34, 60, 80, 0.6);
    box-shadow: 0px 5px 5px -5px rgba(34, 60, 80, 0.6);
  }
  &__navigation-items {
    padding-top: 40px;
    display: flex;
    justify-content: space-between;
  }
  &__list-item:not(:last-child) {
    margin-bottom: 20px;
  }
  &__list-item:first-child {
    padding-top: 40px;
  }
  &__list-item-link {
    font-size: 15px;
    line-height: 1.2;
    font-weight: 400;
    color: #22262a;
    transition: .3s;
  }
  &__list-item-link:hover {
    text-decoration: underline;
    transition: .3s;
  }
}

.contacts__button-next,
  .contacts__button-prev {
    visibility: initial;
  }


@media screen and (max-width: 777px) {
  .contacts {
    &__wrapper {
      padding: 60px 0;
      flex-wrap: wrap;
    }
    &__about-us {
      margin-right: 0;
      width: 100%;
      max-width: initial;
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: space-between;
    }
    &__description {
      display: flex;
      flex-direction: column;
      width: max-content;
      max-width: 350px;
      min-width: 270px;
      width: 50%;
      padding-right: 15px;
    }
    &__we-are {
      display: flex;
      flex-direction: column;
      width: max-content;
      max-width: 350px;
      min-width: 270px;
      width: 50%;
    }
    &__logo {
      width: max-content;
    }
    &__right-content {
      max-width: initial;
    }
    &__in-social {
      padding: 0;
      display: flex;
      justify-content: space-between;
    }
    &__use-svg {
      margin: 15px 0;
      width: 48px;
      height: 48px;
    }
    &__use-svg:not(last-child) {
      margin-right: initial;
    }
    &__recive-email {
      padding-top: 40px;
    }
  }
}

@media screen and (max-width: 575px) {
  .contacts {
    &__description {
      width: 70%;
    }
  }
}

@media screen and (max-width: 444px) {
  .contacts {
    &__wrapper {
      padding-bottom: 80px;
    }
    &__about-us {
      flex-direction: column;
    }
    &__right-content {
      padding-top: 45px;
      position: relative;
    }
    &__navigation {
      display: flex;
      width: 100%;
      overflow: hidden;
    }
    &__navigation-items {
      width: 100%;
    }
    &__navigation-item {
      width: 49%;
      text-align: center;
      flex-shrink: 0;
    }
    &__description {
      width: 100%;
    }
  }

  .contacts__button-next,
  .contacts__button-prev {
    visibility: initial;
    display: flex;
    position: absolute;
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    z-index: 20;
    width: 48px;
    height: 48px;
  }
  .contacts__button-next {
    background-image: url("../img/arrow-right.svg");
    bottom: -65px;
    right: auto;
    left: 55%;
  }
  .contacts__button-prev {
    background-image: url("../img/arrow-left.svg");
    bottom: -65px;
    right: 55%;
    left: auto;
  }
}

@media screen and (max-width: 360px) {
  .contacts {
    &__navigation-item {
      width: 100%;
    }
  }
}