.shop-women {
  background-color: #f2f8fc;
  &__wrapper {
    padding: 100px 0;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__illustrations {
    display: flex;
    justify-content: space-between;
    position: relative;
    width: 100%;
  }
  &__link {
    display: flex;
  }
  &__link:first-child {
    width: 49%;
    padding-top: 65px;
  }
  &__link:nth-child(2) {
    width: 49%;
    min-height: 451px;
    height: max-content;
  }
  &__link:nth-child(3) {
    border-top: 20px solid #ffffff;
    border-left: 20px solid #ffffff;
    position: absolute;
    right: 0;
    bottom: 0;
  }
  &__link:nth-child(3)::before {
    content: "SHOP WOMEN'S DRESS";
    position: absolute;
    height: 18px;
    width: 90%;
    left: -90%;
    bottom: 50px;
    font-size: 15px;
    line-height: 1.2;
    font-weight: 400;
    color: #22262a;
    text-decoration: underline;
  }

  &__image {
    width: 100%;
    transition: .3s;
  }
  &__image:hover {
    -webkit-box-shadow: 5px 5px 5px 1px rgba(34, 60, 80, 0.8);
    -moz-box-shadow: 5px 5px 5px 1px rgba(34, 60, 80, 0.8);
    box-shadow: 5px 5px 5px 1px rgba(34, 60, 80, 0.8);
    transition: .3s;
  }
  
}

@media screen and (max-width: 1000px) {
  .shop-women {
    &__wrapper {
      padding: 60px 0;
    }
    &__illustrations {
      flex-wrap: wrap;
      padding-bottom: 91px;
    }
    &__link:first-child {
      display: none;
      padding-top: 0;
      margin-bottom: 40px;
      width: 100%;
    }
    &__link:nth-child(2) {
      padding-bottom: 60px;
    }
    &__link:nth-child(3) {
      position: relative;
      height: min-content;
      align-self: flex-end;

    }
    &__link:nth-child(3)::before {
      bottom: 20px;
    }
    &__illustrations {
      position: relative;
    }
    &__btn-show {
      display: flex;
      width: 150px;
      position: absolute;
      top: auto;
      bottom: 0;
      left: calc(50% - 150px/2);
      justify-content: center;
    }
  }
}

@media screen and (max-width: 680px) {
  .shop-women {
    &__illustrations {
      padding-bottom: 115px;
    }
    &__link:nth-child(2) {
      display: none;
      width: 100%;
      min-height: initial;
      padding-bottom: 0;
      margin-bottom: 40px;
    }
    &__link:nth-child(3) {
      width: 100%;
      background-color: red;
      height: initial;
      border: none;

    }
    &__link:nth-child(3)::before {
      left: 0;
      bottom: -40px;
      width: 100%;
      text-align: center;
    }
  }
}

