.button {
  padding: 15px 25px;
  background-color: #22262a;
  font-size: calc(16px + (2 + 2 * .7) * ((100vw - 320px)/ 1600)); // 16 18px;
  line-height: 1.2;
  font-weight: 400;
  color: #ffffff;
  transition: .2s
}
.button:hover {
  background-color: #ffffff;
  color: #22262a;
  outline: 1px solid #22262a;
  transform: scale(1.05) translateY(-3px);
  transition: .2s;
}
.button:not(:last-child) {
  margin-right: 40px;
}

@media screen and (max-width: 1000px) {
  .button:not(:last-child) {
    margin-right: 20px;
  }
}