.header {
  display: flex;
  align-items: center;
  height: 85px;
  background-color: #ffffff;
  &__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}



@media screen and (max-width: 1000px) {
  .header {
    &__logo {
      padding-left: 3vw;
    }
  }
}