  font-weight: 700;
  color: #22262a;
}

.title_pre {
  font-size: 18px;
  margin: 30px 0;
  line-height: 1.2;
  font-weight: 400;
  color: #22262a;
}

.title_big {
  font-size: calc(50px + (30 + 30 * .7) * ((100vw - 320px)/ 1600));
  line-height: 1.2;
  font-weight: 700;
  color: #22262a;
}

.text {
  padding-top: 15px;
  padding-bottom: 55px;
