.fashion-categories {
  background-color: #f2f8fc;
}

.fashion-categories__wrapper {
  padding: 120px 0 50px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-gap: 30px;
}

.fashion-categories__item {
  position: relative;
  z-index: 0;
  transition: .3s;
}

.fashion-categories__item:hover .fashion-categories__effect {
  z-index: 1;
}

.fashion-categories__effect {
  display: flex;
  position: absolute;
  z-index: -1;
  width: 100%;
  height: 100%;
  background: #201f1fd2;
  top: 0;
  left: 0;
  transition: .3s;
}

.fashion-categories__label {
  margin: auto auto 20px 20px;
  padding: 15px 25px;
  background-color: #fafafa;
  font-size: 18px;
  line-height: 21px;
  font-weight: 400;
  letter-spacing: 0.025em;
  color: #22262a;
}

.fashion-categories__image {
  width: 100%;
  height: 100%;
}

.fashion-categories__item:nth-child(1) {
  grid-column: 1/3;
}

.fashion-categories__item:nth-child(2) {
  grid-column: 3/5;
  grid-row: 1/3;
}

@media screen and (max-width: 1000px) {
  .fashion-categories__wrapper {
    padding-top: 60px;
  }
}

@media screen and (max-width: 740px) {
  .fashion-categories__wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    overflow: hidden;
    max-width: min-content;
  }
  .fashion-categories__image {
    width: 100%;
    flex-shrink: 1;
  }
  .fashion-categories__effect {
    z-index: 1;
    background-color: transparent;
  }
  .fashion-categories__item:nth-child(1) {
    width: 100%;
    display: block;
  }
  .fashion-categories__item:nth-child(2) {
    width: 47%;
    color: red;
  }
  .fashion-categories__item:nth-child(3) {
    width: 47%;
  }
  .fashion-categories__item:nth-child(4) {
    width: 100%;
    display: none;
  }
  .fashion-categories__btn-show {
    display: block;
  }
}

@media screen and (max-width: 535px) {
  .fashion-categories__item:nth-child(3) {
    width: 100%;
