.shop-collection {
  background-image: url(../img/shopCollection.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  height: 100vh;
  min-height: 568px;
  &__wrapper {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    height: 100%;
  }

  &__content {
    display: flex;
    flex-direction: column;
    align-items: start;
    padding-bottom: 100px;
  }
}

@media screen and (max-width: 820px) {
  .shop-collection {
    background-position-x: 70%;
    &__content {
      padding-bottom: 60px;
    }
  }
}